<template>
  <div class="rating">
    <input type="radio" name="rating" value="5" id="5" />
    <label for="5" v-on:click="UpdateRating(5)">☆</label>
    
    <input type="radio" name="rating" value="4" id="4" />
    <label for="4" v-on:click="UpdateRating(4)">☆</label>
    
    <input type="radio" name="rating" value="3" id="3" />
    <label for="3" v-on:click="UpdateRating(3)">☆</label>
    
    <input type="radio" name="rating" value="2" id="2" />
    <label for="2" v-on:click="UpdateRating(2)">☆</label>
    
    <input type="radio" name="rating" value="1" id="1" />
    <label for="1" v-on:click="UpdateRating(1)">☆</label>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {

  name: "Stars",
  computed: {
    ...mapState(["starToReview"])
  },
  methods: {
    UpdateRating(rating) {
        this.$store.dispatch("updateRating", { self: this, rating });
    }
  }
};
</script>

<style scoped>
.rating {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.rating > input {
  display: none;
}

.rating > label {
  position: relative;
  width: 1.5em;
  font-size: 3vw;
  color: #f5c30b;
  cursor: pointer;
}

.rating > label::before {
  content: "\2605";
  position: absolute;
  opacity: 0;
}

.rating > label:hover:before,
.rating > label:hover ~ label:before {
  opacity: 1 !important;
}

.rating > input:checked ~ label:before {
  opacity: 1;
}

.rating:hover > input:checked ~ label:before {
  opacity: 0.4;
}

body {
  background: #222225;
  color: white;
}

h1,
p {
  text-align: center;
}

h1 {
  margin-top: 150px;
}

p {
  font-size: 0.1rem;
}

@media only screen and (max-width: 600px) {
  h1 {
    font-size: 14px;
  }

  p {
    font-size: 12px;
  }
}
</style>